import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';

import { Link } from 'react-router-dom';
import {
  Button,
  Row,
  Col,
  ListGroup,
  Image,
  Card,
  Alert,
} from 'react-bootstrap';

import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import Loader from '../components/Loader';

const OrderSummaryScreen = () => {
  const { id } = useParams();
  const [orderDetails, setOrderDetails] = useState();

  const [success, setSuccess] = useState(false);

  const user = useSelector((state) => state.data.user.loggedInUser);

  const getOrderSummary = async () => {
    try {
      const order = await axios.get(`/api/orders/${id}`, {
        headers: {
          Authorization: `Bearer ${user.token}`,
        },
      });

      // console.log(order.data);
      setOrderDetails(order.data);
      //   setOrders(allOrders.data);
    } catch (error) {
      // console.log(error.response.data.msg);
    }
  };

  useEffect(() => {
    getOrderSummary();
  }, []);

  return (
    <>
      {orderDetails ? (
        <>
          <h1>Order : {orderDetails._id}</h1>
          <Row>
            <Col md={8}>
              <ListGroup variant='flush'>
                <ListGroup.Item>
                  <h2>Shipping</h2>
                  <p>
                    <strong>Address:</strong>
                    {orderDetails.shippingAddress.address},{' '}
                    {orderDetails.shippingAddress.city}{' '}
                    {orderDetails.shippingAddress.postalCode},{' '}
                    {orderDetails.shippingAddress.country}
                  </p>
                </ListGroup.Item>

                <ListGroup.Item>
                  <h2 className='my-2'>Payment Method</h2>
                  <strong>Method: </strong>
                  {orderDetails.paymentMethod}
                </ListGroup.Item>

                <ListGroup.Item>
                  <h2 className='my-2'>Order Items</h2>
                  <ListGroup variant='flush'>
                    {orderDetails.orderItems.map((item, index) => (
                      <ListGroup.Item key={index}>
                        <Row>
                          <Col md={1}>
                            <Image
                              src={`${item.image}`.replace('/server', '')}
                              alt={item.name}
                              fluid
                              rounded
                            />
                          </Col>
                          <Col>
                            <Link to={`/product/${item._id}`}>{item.name}</Link>
                          </Col>
                          <Col md={4}>
                            {item.quantity} x ${item.price} = $
                            {(item.quantity * item.price).toFixed(2)}
                          </Col>
                        </Row>
                      </ListGroup.Item>
                    ))}
                  </ListGroup>
                </ListGroup.Item>
              </ListGroup>
            </Col>

            <Col md={4}>
              <Card>
                <ListGroup variant='flush'>
                  <ListGroup.Item>
                    <h2>Order Summary</h2>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Items</Col>
                      <Col>
                        $
                        {orderDetails.totalPrice -
                          orderDetails.shippingPrice -
                          orderDetails.taxPrice}
                      </Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Shipping</Col>
                      <Col>${orderDetails.shippingPrice}</Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Tax</Col>
                      <Col>${orderDetails.taxPrice}</Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Total</Col>
                      <Col>${orderDetails.totalPrice}</Col>
                    </Row>
                  </ListGroup.Item>
                </ListGroup>
                <Alert variant='info'>
                  Paid On :{' '}
                  {orderDetails.paidAt ? orderDetails.paidAt : 'Not Paid'}
                </Alert>
              </Card>
            </Col>
          </Row>
        </>
      ) : (
        <Loader />
      )}
    </>
  );
};

export default OrderSummaryScreen;
