import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import {
  Table,
  Form,
  Button,
  Row,
  Col,
  Container,
  Alert,
  Modal,
} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Loader from '../components/Loader';

const ProductListScreen = () => {
  const [products, setProducts] = useState();
  const [error, setError] = useState();

  const navigate = useNavigate();

  const user = useSelector((state) => state.data.user.loggedInUser);

  const getAllProducts = async () => {
    try {
      const allProducts = await axios.get('/api/products');

      //   console.log(allProducts.data.products);
      setProducts(allProducts.data.products);
    } catch (error) {
      setError(error.response.data.msg);
    }
  };

  useEffect(() => {
    getAllProducts();
  }, []);

  const addProductHandler = async () => {
    const newProduct = await axios.post(
      '/api/products',
      {},
      {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${user.token}`,
        },
      }
    );

    if (newProduct.status === 201) {
      navigate(`/admin/product/edit/${newProduct.data._id}`);
    }
  };

  const deleteHandler = async (id) => {
    if (window.confirm('Please provide confirmation ?')) {
      const deletedUser = await axios.delete(`/api/products/${id}`, {
        headers: {
          Authorization: `Bearer ${user.token}`,
        },
      });
      getAllProducts();
    }
  };

  return (
    <>
      {products ? (
        <>
          <Row className='align-items-center'>
            <Col>
              <h1>Products</h1>
            </Col>
            <Col className='d-flex justify-content-end'>
              <Button className='my-3' onClick={addProductHandler}>
                <i className='fas fa-plus'></i> Add Product
              </Button>
            </Col>
          </Row>
          <Table striped bordered hover responsive className='table-sm'>
            <thead>
              <tr>
                <th>ID</th>
                <th>NAME</th>
                <th>PRICE</th>
                <th>CATEGORY</th>
                <th>BRAND</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {products.map((product) => (
                <tr key={product._id}>
                  <td>{product._id}</td>
                  <td>{product.name}</td>
                  <td>${product.price}</td>
                  <td>{product.category}</td>
                  <td>{product.brand}</td>
                  <td>
                    <Button
                      variant='light'
                      className='btn-sm'
                      as={Link}
                      to={`/admin/product/edit/${product._id}`}
                    >
                      <i className='fas fa-edit'></i>
                    </Button>

                    <Button
                      variant='danger'
                      className='btn-sm'
                      onClick={() => deleteHandler(product._id)}
                    >
                      <i className='fas fa-trash'></i>
                    </Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </>
      ) : (
        <Loader />
      )}
    </>
  );
};

export default ProductListScreen;
