import React, { useState, useEffect } from 'react';
import { Link, useSearchParams } from 'react-router-dom';
import { Form, Button, Row, Col, Container, Alert } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import Loader from '../components/Loader';
import { userLogin } from '../features/user/userSlice';
import { useNavigate } from 'react-router-dom';
import { setErrorMsg } from '../features/user/userSlice';

const LoginScreen = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [searchParams, setSearchParams] = useSearchParams();

  const user = useSelector((state) => state.data.user.loggedInUser);

  const error = useSelector((state) => state.data.user.error);
  const success = useSelector((state) => state.data.user.success);

  const redirect = searchParams.get('redirect')
    ? `/${searchParams.get('redirect')}`
    : '/';

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(userLogin({ email, password }));

    // navigate('/');
  };

  useEffect(() => {
    dispatch(setErrorMsg());
  }, []);

  useEffect(() => {
    // console.log(user);
    // console.log('looged in');
    if (Object.keys(user).length !== 0) {
      // console.log(redirect);
      navigate(redirect);
      // navigate(redirect);
    }
  }, [success]);

  return (
    <Container>
      <Row className='justify-content-md-center'>
        <Col xs={12} md={6}>
          <h1>Sign In</h1>

          {error !== '' ? <Alert variant='danger'>{error}</Alert> : <></>}
          <Form onSubmit={submitHandler}>
            <Form.Group controlId='email'>
              <Form.Label>Email Address</Form.Label>
              <Form.Control
                type='email'
                placeholder='Enter email'
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              ></Form.Control>
            </Form.Group>

            <Form.Group controlId='password'>
              <Form.Label className='my-2'>Password</Form.Label>
              <Form.Control
                type='password'
                placeholder='Enter password'
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              ></Form.Control>
            </Form.Group>

            <Button type='submit' variant='primary' className='my-3'>
              Sign In
            </Button>
          </Form>
          <Row className='py-3'>
            <Col>
              New Customer? <Link to={'/register'}>Register</Link>
            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
  );
};

export default LoginScreen;
