import React, { useEffect } from 'react';
import {
  useParams,
  useSearchParams,
  Link,
  useNavigate,
} from 'react-router-dom';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { addItemToCart } from '../features/products/productsSlice';
import { removeItemFromCart } from '../features/products/productsSlice';
import {
  Alert,
  ListGroup,
  Row,
  Col,
  ListGroupItem,
  Image,
  Form,
  Button,
  Card,
} from 'react-bootstrap';

const CartScreen = () => {
  const Dispatch = useDispatch();
  const cart = useSelector((state) => state.data.products.cart.cartItems);

  //   console.log(cart);
  const { id } = useParams();
  const [searchParams, setSearchParams] = useSearchParams();
  const quantity = searchParams.get('qty');
  const modification = 'add';
  const navigate = useNavigate();

  //   console.log(id);
  //   console.log(quantity);

  useEffect(() => {
    if (id) {
      Dispatch(addItemToCart({ id, quantity, modification }));
    }

    navigate('/cart');
  }, []);

  const removeFromCart = (id) => {
    Dispatch(removeItemFromCart({ id }));
  };

  const checkoutHandler = () => {
    navigate('/login?redirect=shipping');
  };

  return (
    <Row>
      <Col md={8}>
        <h1>Shopping Cart</h1>
        <Link className='btn btn-light my-3' to='/'>
          Go back
        </Link>
        {cart.length != 0 ? (
          <ListGroup variant='flush'>
            {cart.map((product) => (
              <ListGroupItem key={product._id}>
                <Row>
                  <Col md={2}>
                    <Image
                      src={`${product.image}`.replace('/server', '')}
                      alt={product.name}
                      fluid
                      rounded
                    ></Image>
                  </Col>
                  <Col md={3}>
                    <Link to={`/product/${product._id}`}>{product.name}</Link>
                  </Col>
                  <Col md={2}>${product.price}</Col>
                  <Col md={2}>
                    <Form.Control
                      as='select'
                      value={product.quantity}
                      onChange={(e) =>
                        Dispatch(
                          addItemToCart({
                            id: product._id,
                            quantity: Number(e.target.value),
                            modification: 'change',
                          })
                        )
                      }
                    >
                      {[...Array(product.countInStock).keys()].map((x) => (
                        <option key={x + 1} value={x + 1}>
                          {x + 1}
                        </option>
                      ))}
                    </Form.Control>
                  </Col>
                  <Col md={2}>
                    <Button
                      type='button'
                      variant='light'
                      onClick={() => removeFromCart(product._id)}
                    >
                      <i className='fas fa-trash'></i>
                    </Button>
                  </Col>
                </Row>
              </ListGroupItem>
            ))}
          </ListGroup>
        ) : (
          <Alert variant='info'>Cart Is Empty!!!!</Alert>
        )}
      </Col>
      <Col md={4}>
        <Card>
          <ListGroup variant='flush'>
            <ListGroupItem>
              <h3>
                Subtotal (
                {cart.reduce((acc, item) => acc + Number(item.quantity), 0)})
                items
              </h3>
              $
              {cart
                .reduce((acc, item) => acc + item.quantity * item.price, 0)
                .toFixed(2)}
            </ListGroupItem>
            <ListGroupItem>
              <Button
                type='button'
                className='btn-block'
                disabled={cart.length === 0}
                onClick={checkoutHandler}
              >
                Proceed to checkout
              </Button>
            </ListGroupItem>
          </ListGroup>
        </Card>
      </Col>
    </Row>
  );
};

export default CartScreen;
