import React, { useState } from 'react';
import { Form, Button, Col, Container, Row } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import CheckoutBreadcrumbs from '../components/CheckoutBreadcrumbs';
import { setPaymentType } from '../features/products/productsSlice';

const PaymentScreen = () => {
  const [paymentMethod, setPaymentMethod] = useState('RazorPay');
  const Dispatch = useDispatch();
  const navigate = useNavigate();

  const submitHandler = (e) => {
    e.preventDefault();
    Dispatch(setPaymentType(paymentMethod));
    navigate('/placeorder');
  };
  return (
    <Container>
      <Row className='justify-content-md-center'>
        <CheckoutBreadcrumbs step1 step2 step3 />
        <Col xs={12} md={6}>
          <h1>Payment Method</h1>
          <Form onSubmit={submitHandler}>
            <Form.Group>
              <Form.Label as='legend' className='my-3'>
                Select Payment Method
              </Form.Label>
              <Col>
                <Form.Check
                  type='radio'
                  label='Razorpay UPI,DEBIT or Credit Card'
                  id='Razorpay'
                  name='paymentMethod'
                  value='Razorpay'
                  checked
                  onChange={(e) => setPaymentMethod(e.target.value)}
                  className='my-3'
                ></Form.Check>
              </Col>
            </Form.Group>

            <Button type='submit' variant='primary'>
              Continue
            </Button>
          </Form>
        </Col>
      </Row>
    </Container>
  );
};

export default PaymentScreen;
