import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import {
  Form,
  Button,
  Row,
  Col,
  Container,
  Alert,
  Modal,
} from 'react-bootstrap';

import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import Loader from '../components/Loader';

const UserEditScreen = () => {
  const { id } = useParams();
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [isAdmin, setIsAdmin] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState(false);

  const user = useSelector((state) => state.data.user.loggedInUser);

  const getUserDetails = async () => {
    const userDetail = await axios.get(`/api/user/${id}`, {
      headers: {
        Authorization: `Bearer ${user.token}`,
      },
    });

    setName(userDetail.data.name);
    setEmail(userDetail.data.email);
    setIsAdmin(userDetail.data.isAdmin);
  };

  useEffect(() => {
    getUserDetails();
  }, []);

  const submitHandler = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.put(
        `/api/user/${id}`,
        JSON.stringify({ name, email, isAdmin }),
        {
          headers: {
            Authorization: `Bearer ${user.token}`,
            'Content-Type': 'application/json',
          },
        }
      );

      if (res.status === 200) {
        setSuccess(true);
        setError('');
      }

      // console.log(res);
    } catch (err) {
      setError(err.response.data.message);
    }
  };

  return (
    <>
      <Link to='/admin/userlist' className='btn btn-light my-3'>
        Go Back
      </Link>

      {success ? (
        <Modal
          size='sm'
          show={success}
          onHide={() => setSuccess(false)}
          aria-labelledby='example-modal-sizes-title-sm'
        >
          <Modal.Header closeButton>
            <Modal.Title id='example-modal-sizes-title-sm'>
              Successfull
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>Infomation Updated Successfully!!</Modal.Body>
        </Modal>
      ) : (
        <></>
      )}
      {error !== '' ? <Alert variant='danger'>{error}</Alert> : <></>}

      <Container>
        <Row className='justify-content-md-center'>
          <Col xs={12} md={6}>
            <h1>Edit User</h1>

            {!name ? (
              <Loader></Loader>
            ) : (
              <>
                <Form onSubmit={submitHandler}>
                  <Form.Group controlId='name'>
                    <Form.Label>Name</Form.Label>
                    <Form.Control
                      type='name'
                      placeholder='Enter name'
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                    ></Form.Control>
                  </Form.Group>

                  <Form.Group controlId='email' className='my-8'>
                    <Form.Label className='my-3'>Email Address</Form.Label>
                    <Form.Control
                      type='email'
                      placeholder='Enter email'
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                    ></Form.Control>
                  </Form.Group>

                  <Form.Group controlId='isadmin' className='my-3'>
                    <Form.Check
                      type='checkbox'
                      label='Is Admin'
                      checked={isAdmin}
                      onChange={(e) => setIsAdmin(e.target.checked)}
                    ></Form.Check>
                  </Form.Group>

                  <Button type='submit' variant='primary'>
                    Update
                  </Button>
                </Form>
              </>
            )}
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default UserEditScreen;
