import Carousel from 'react-bootstrap/Carousel';
import slide1video from '../images/slide.mp4';
import slide2video from '../images/slide2.mp4';
import slide4video from '../images/slide6.mp4';
import { Link } from 'react-router-dom';

function ProductCarousel() {
  return (
    <Carousel>
      <Carousel.Item>
        {/* <img className='d-block w-100' src='/images/s1.jpg' alt='First slide' /> */}
        <video
          src={slide2video}
          width='100%'
          height='70%'
          autoPlay
          muted
          loop
        />
        <Carousel.Caption>
          <h3 style={{ color: 'white' }}>iPhone 14 Pro</h3>
          <p>Pro.Beyond</p>
          <Link
            to={`/product/644ce019222d9d0c99d30b11`}
            style={{ color: 'white' }}
          >
            Learn More
          </Link>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <video
          src={slide1video}
          width='100%'
          height='70%'
          autoPlay
          muted
          loop
        />

        <Carousel.Caption>
          <h3 style={{ color: 'white' }}>AirPods Pro</h3>
          <Link
            to={`/product/644ce019222d9d0c99d30b10`}
            style={{ color: 'white' }}
          >
            Learn More
          </Link>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <video
          src={slide4video}
          width='100%'
          height='90%'
          autoPlay
          muted
          loop
        />

        <Carousel.Caption>
          <h3>iMac 24''</h3>
          <Link
            to={`/product/644cee9df4a968b5c7d7b489`}
            style={{ color: 'black' }}
          >
            Learn More
          </Link>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}

export default ProductCarousel;
