import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import {
  Row,
  Col,
  Image,
  ListGroup,
  Card,
  Button,
  ListGroupItem,
  Form,
  Alert,
} from 'react-bootstrap';
import Loader from '../components/Loader';
import Rating from '../components/Rating';
import { fetchAllProducts } from '../features/products/productsSlice';

const ProductScreen = () => {
  const { id } = useParams();
  const Dispatch = useDispatch();

  const { products } = useSelector((state) => state.data.products);
  const cart = useSelector((state) => state.data.products.cart.cartItems);
  const user = useSelector((state) => state.data.user.loggedInUser);

  const [product, setProduct] = useState({});
  const [qty, setQty] = useState(1);
  const [cartQuantity, setCartQuantity] = useState(0);
  const [rating, setRating] = useState(0);
  const [comment, setComment] = useState('');
  const [reviewStatus, setReviewStatus] = useState(false);
  const [reviewError, setReviewError] = useState('');

  const opt = [];
  const navigate = useNavigate();

  if (product.countInStock > 0) {
    for (let i = 0; i < product.countInStock - cartQuantity; i++) {
      opt.push(i + 1);
    }
  }
  //   const product = products.find((p) => p._id === id);

  useEffect(() => {
    products.map((product) => {
      if (product._id === id) setProduct(product);
    });

    cart.map((product) => {
      if (product._id === id) setCartQuantity(product.quantity);
    });
  }, [id]);

  const submitHandler = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.post(
        `/api/products/${id}/reviews`,
        JSON.stringify({ rating, comment }),
        {
          headers: {
            Authorization: `Bearer ${user.token}`,
            'Content-Type': 'application/json',
          },
        }
      );

      Dispatch(fetchAllProducts());

      setReviewStatus(true);
    } catch (error) {
      // console.log(error.response.data.message);
      setReviewError(error.response.data.message);
    }
  };

  return (
    <>
      {product ? (
        <>
          <Link className='btn btn-light my-3' to='/'>
            Go back
          </Link>
          <Row>
            <Col md={6}>
              <Image
                src={`${product.image}`.replace('/server', '')}
                alt={product.name}
                fluid
              />
            </Col>
            <Col md={3}>
              <ListGroup variant='flush'>
                <ListGroupItem>
                  <h3>{product.name}</h3>
                </ListGroupItem>
                <ListGroupItem>
                  <Rating
                    rating={product.rating}
                    noReviews={product.numReviews}
                  />
                </ListGroupItem>
                <ListGroupItem>Price : ${product.price}</ListGroupItem>
                <ListGroupItem>Description:{product.description}</ListGroupItem>
              </ListGroup>
            </Col>
            <Col md={3}>
              <Card>
                <ListGroup variant='flush'>
                  <ListGroupItem>
                    <Row>
                      <Col>Pice:</Col>
                      <Col>
                        <strong>${product.price}</strong>
                      </Col>
                    </Row>
                  </ListGroupItem>

                  <ListGroupItem>
                    <Row>
                      <Col>Status:</Col>
                      <Col>
                        {product.countInStock !== cartQuantity
                          ? 'In Stock'
                          : 'Out of Stock'}
                      </Col>
                    </Row>
                  </ListGroupItem>

                  {product.countInStock !== cartQuantity && (
                    <ListGroupItem>
                      <Row>
                        <Col>Qty:</Col>
                        <Col>
                          <Form.Control
                            as='select'
                            value={qty}
                            onChange={(e) => setQty(e.target.value)}
                          >
                            {opt.map((i) => (
                              <option value={i} key={i}>
                                {i}
                              </option>
                            ))}
                          </Form.Control>
                        </Col>
                      </Row>
                    </ListGroupItem>
                  )}

                  <ListGroupItem>
                    <Row>
                      <Button
                        onClick={
                          () => navigate(`/cart/${product._id}?qty=${qty}`)

                          // history.push(`/cart/${product._id}?qty=${qty}`)
                        }
                        className='btn-block'
                        type='button'
                        disabled={product.countInStock === cartQuantity}
                      >
                        Add to Cart
                      </Button>
                    </Row>
                  </ListGroupItem>
                </ListGroup>
              </Card>
            </Col>
          </Row>

          {product.reviews ? (
            <Row>
              <Col md={6} className='mt-5'>
                <h2>Reviews</h2>

                {product.reviews.length === 0 && (
                  <Alert variant='info'>No Reviews</Alert>
                )}
                <ListGroup variant='flush'>
                  {product.reviews.map((review) => (
                    <ListGroup.Item key={review._id}>
                      <strong>{review.name}</strong>
                      <Rating rating={review.rating} />
                      <p>{review.createdAt.substring(0, 10)}</p>
                      <p>Comment : {review.comment}</p>
                    </ListGroup.Item>
                  ))}

                  <ListGroup.Item>
                    <h3>Write Review</h3>
                    {reviewStatus && (
                      <Alert variant='success'>Review Added</Alert>
                    )}
                    {reviewError && (
                      <Alert variant='danger'>{reviewError}</Alert>
                    )}
                    {!(user && Object.keys(user).length === 0) ? (
                      <>
                        <Form onSubmit={submitHandler}>
                          <Form.Group controlId='rating'>
                            <Form.Label>Rating</Form.Label>
                            <Form.Control
                              as='select'
                              value={rating}
                              onChange={(e) => setRating(e.target.value)}
                            >
                              <option value=''>Select...</option>
                              <option value='1'>1 - Poor</option>
                              <option value='2'>2 - Fair</option>
                              <option value='3'>3 - Good</option>
                              <option value='4'>4 - Very Good</option>
                              <option value='5'>5 - Excellent</option>
                            </Form.Control>
                          </Form.Group>
                          <Form.Group controlId='comment'>
                            <Form.Label>Comment</Form.Label>
                            <Form.Control
                              as='textarea'
                              row='3'
                              value={comment}
                              onChange={(e) => setComment(e.target.value)}
                            ></Form.Control>
                          </Form.Group>
                          <Button
                            disabled={false}
                            type='submit'
                            variant='primary'
                          >
                            Submit
                          </Button>
                        </Form>
                      </>
                    ) : (
                      <Alert variant='warning'>
                        Please <Link to='/login'>sign in</Link> to write a
                        review{' '}
                      </Alert>
                    )}
                  </ListGroup.Item>
                </ListGroup>
              </Col>
            </Row>
          ) : (
            <Loader />
          )}
        </>
      ) : (
        <Loader />
      )}
    </>
  );
};

export default ProductScreen;
