import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Table,
  Form,
  Button,
  Row,
  Col,
  Container,
  Alert,
  Modal,
} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Loader from '../components/Loader';

import { updateUser } from '../features/user/userSlice';

import { useDispatch, useSelector } from 'react-redux';

const ProfileScreen = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState(false);
  const [userOrders, setUserOrders] = useState();

  const user = useSelector((state) => state.data.user.loggedInUser);

  const Dispatch = useDispatch();

  const getOrders = async () => {
    try {
      const orders = await axios.get('/api/orders/user', {
        headers: {
          Authorization: `Bearer ${user.token}`,
        },
      });

      setUserOrders(orders.data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    setName(user.name);
    setEmail(user.email);

    getOrders();

    setSuccess(false);
  }, []);

  const submitHandler = async (e) => {
    // console.log(name, email, password);
    e.preventDefault();
    if (password !== confirmPassword) {
      setError('Password and ConfirmPassword not Matched!!!');
    } else {
      try {
        const res = await axios.put(
          '/api/user/profile',
          JSON.stringify({ name, email, password }),
          {
            headers: {
              Authorization: `Bearer ${user.token}`,
              'Content-Type': 'application/json',
            },
          }
        );

        if (res.status === 200) {
          setSuccess(true);
          setError('');
          Dispatch(updateUser({ name, email }));
        }

        // console.log(res);
      } catch (err) {
        setError(err.response.data.message);
      }
    }
  };
  return (
    <Row>
      <Col md={3}>
        {success ? (
          <Modal
            size='sm'
            show={success}
            onHide={() => setSuccess(false)}
            aria-labelledby='example-modal-sizes-title-sm'
          >
            <Modal.Header closeButton>
              <Modal.Title id='example-modal-sizes-title-sm'>
                Successfull
              </Modal.Title>
            </Modal.Header>
            <Modal.Body>Infomation Updated Successfully!!</Modal.Body>
          </Modal>
        ) : (
          <></>
        )}
        {error !== '' ? <Alert variant='danger'>{error}</Alert> : <></>}
        <h2>Profile</h2>
        <Form onSubmit={submitHandler}>
          <Form.Group controlId='name'>
            <Form.Label>Name</Form.Label>
            <Form.Control
              type='name'
              placeholder='Enter name'
              value={name}
              onChange={(e) => setName(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Form.Group controlId='email'>
            <Form.Label>Email Address</Form.Label>
            <Form.Control
              type='email'
              placeholder='Enter email'
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Form.Group controlId='password'>
            <Form.Label>Password</Form.Label>
            <Form.Control
              type='password'
              placeholder='Enter password'
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Form.Group controlId='confirmPassword'>
            <Form.Label>Confirm Password</Form.Label>
            <Form.Control
              type='password'
              placeholder='Confirm password'
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Button type='submit' variant='primary' className='my-3'>
            Update
          </Button>
        </Form>
      </Col>

      <Col md={9}>
        <h2>Orders</h2>

        {!userOrders ? (
          <Loader />
        ) : (
          <>
            <Table striped bordered hover responsive className='table-sm'>
              <thead>
                <tr>
                  <th>ID</th>
                  <th>DATE</th>
                  <th>TOTAL</th>
                  <th>PAID</th>
                  <th>DELIVERED</th>
                  <th></th>
                </tr>
              </thead>

              <tbody>
                {userOrders.map((order) => (
                  <tr key={order._id}>
                    <td>{order._id}</td>
                    <td>{order.createdAt.substring(0, 10)}</td>
                    <td>{order.totalPrice}</td>
                    <td>
                      {order.isPaid ? (
                        order.paidAt.substring(0, 10)
                      ) : (
                        <i
                          className='fas fa-times'
                          style={{ color: 'red' }}
                        ></i>
                      )}
                    </td>
                    <td>
                      {order.isDelivered ? (
                        order.deliveredAt.substring(0, 10)
                      ) : (
                        <i
                          className='fas fa-times'
                          style={{ color: 'red' }}
                        ></i>
                      )}
                    </td>
                    <td>
                      <Link to={`/ordersummary/${order._id}`}>Details</Link>
                    </td>
                  </tr>
                ))}
              </tbody>
            </Table>

            {userOrders.length === 0 ? (
              <Alert variant='info'>No orders</Alert>
            ) : (
              <></>
            )}
          </>
        )}
      </Col>
    </Row>
  );
};

export default ProfileScreen;
