import React from 'react';

const Rating = ({ rating, noReviews, color }) => {
  // console.log(rating);
  return (
    // <div>
    //   <i className='fa-solid fa-star' />
    //   <i className='fa-regular fa-star-half-stroke' />
    //   <i className='fa-regular fa-star' />
    // </div>

    <div className='ratings'>
      <span>
        <i
          style={{ color }}
          className={
            rating >= 1
              ? 'fa-solid fa-star'
              : rating >= 0.5
              ? 'fa-regular fa-star-half-stroke'
              : 'fa-regular fa-star'
          }
        />
      </span>

      <span className='ratings'>
        <i
          style={{ color }}
          className={
            rating >= 2
              ? 'fa-solid fa-star'
              : rating >= 1.5
              ? 'fa-regular fa-star-half-stroke'
              : 'fa-regular fa-star'
          }
        />
      </span>

      <span className='ratings'>
        <i
          style={{ color }}
          className={
            rating >= 3
              ? 'fa-solid fa-star'
              : rating >= 2.5
              ? 'fa-regular fa-star-half-stroke'
              : 'fa-regular fa-star'
          }
        />
      </span>

      <span className='ratings'>
        <i
          style={{ color }}
          className={
            rating >= 4
              ? 'fa-solid fa-star'
              : rating >= 3.5
              ? 'fa-regular fa-star-half-stroke'
              : 'fa-regular fa-star'
          }
        />
      </span>

      <span className='ratings'>
        <i
          style={{ color }}
          className={
            rating >= 5
              ? 'fa-solid fa-star'
              : rating >= 4.5
              ? 'fa-regular fa-star-half-stroke'
              : 'fa-regular fa-star'
          }
        />
      </span>

      <span>&nbsp;{noReviews}Reviews</span>
    </div>
  );
};

Rating.defaultProps = {
  color: '#f8e82',
};

export default Rating;
