import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import { Link } from 'react-router-dom';
import {
  Button,
  Row,
  Col,
  ListGroup,
  Image,
  Card,
  Alert,
} from 'react-bootstrap';
import orderPlaced from '../Data/success.gif';
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import CheckoutBreadcrumbs from '../components/CheckoutBreadcrumbs';
import { emptyCart } from '../features/products/productsSlice.js';
import Loader from '../components/Loader';

const PlaceOrderScreen = () => {
  const Dispatch = useDispatch();

  const [loader, setLoader] = useState(false);

  const [success, setSuccess] = useState(false);
  const navigate = useNavigate();
  const cart = useSelector((state) => state.data.products.cart);

  const user = useSelector((state) => state.data.user.loggedInUser);

  const [paymentStatus, setPaymentStatus] = useState(false);

  const { cartItems, shippingDetails, paymentMethod } = cart;

  const [totalPrice, setTotalPrice] = useState(() =>
    cart.cartItems
      .reduce((acc, item) => acc + item.price * item.quantity, 0)
      .toFixed(2)
  );

  // const itemsPrice = cart.cartItems
  //   .reduce((acc, item) => acc + item.price * item.quantity, 0)
  //   .toFixed(2);

  const shippingCharge = totalPrice > 100 ? 0 : 10;

  const tax = (0.15 * totalPrice).toFixed(2);

  const total = (
    Number(totalPrice) +
    Number(shippingCharge) +
    Number(tax)
  ).toFixed(2);

  const loadScript = (src) => {
    return new Promise((resolve) => {
      const script = document.createElement('script');
      script.src = src;
      script.onload = () => {
        resolve(true);
      };
      script.onerror = () => {
        resolve(false);
      };
      document.body.appendChild(script);
    });
  };

  const displayRazorpay = async ({ dbOrderId }) => {
    try {
      const res = await loadScript(
        'https://checkout.razorpay.com/v1/checkout.js'
      );

      if (!res) {
        alert('Razorpay SDK failed to load. Are you online?');
        return;
      }

      const result = await axios.post(
        '/api/payment/orders',
        JSON.stringify({
          amount: Math.round(total),
        }),
        {
          headers: {
            Authorization: `Bearer ${user.token}`,
            'Content-Type': 'application/json',
          },
        }
      );

      if (!result) {
        alert('Server error. Are you online?');
        return;
      }

      const { amount, id: order_id, currency } = result.data.data;

      const options = {
        key: 'rzp_test_eUNyD2y8gI5ECW', // Enter the Key ID generated from the Dashboard
        amount: amount.toString(),
        currency: currency,
        name: 'E-Cart PVT LTD',
        description: ' Transaction',

        order_id: order_id,
        handler: async function (response) {
          const data = {
            order_id: order_id,
            razorpay_payment_id: response.razorpay_payment_id,
            razorpay_order_id: response.razorpay_order_id,
            razorpay_signature: response.razorpay_signature,
          };

          const result = await axios.post(
            `/api/payment/verify/${dbOrderId}`,
            JSON.stringify({
              data,
            }),
            {
              headers: {
                Authorization: `Bearer ${user.token}`,
                'Content-Type': 'application/json',
              },
            }
          );

          // alert(result.data.msg);
          setLoader(false);
          setPaymentStatus(true);

          // setTimeout(() => navigate('/'), 5000);
          Dispatch(emptyCart());
        },
        prefill: {
          name: user.name,
          email: user.email,
          contact: '9999999999',
        },
        notes: {
          address: 'XYZ',
        },
        theme: {
          color: '#61dafb',
        },
      };

      const paymentObject = new window.Razorpay(options);
      paymentObject.open();
    } catch (error) {
      // console.log(error);
    }
  };

  const placeOrderHandler = async () => {
    setLoader(true);

    try {
      const res = await axios.post(
        '/api/orders/',
        JSON.stringify({
          cartItems,
          shippingDetails,
          paymentMethod,
          totalPrice,
          tax,
          shippingCharge,
          total,
        }),
        {
          headers: {
            Authorization: `Bearer ${user.token}`,
            'Content-Type': 'application/json',
          },
        }
      );

      if (res.status === 200 || 201) {
        setSuccess(true);

        displayRazorpay({ dbOrderId: res.data._id });
      }
    } catch (err) {
      // console.log(err);
    }
  };
  return (
    <>
      {loader ? (
        <Loader />
      ) : (
        <>
          {!paymentStatus ? (
            <CheckoutBreadcrumbs step1 step2 step3 step4 />
          ) : (
            <></>
          )}

          <Row>
            <Col md={8}>
              {!paymentStatus ? (
                <>
                  <ListGroup variant='flush'>
                    <ListGroup.Item>
                      <h2>Shipping</h2>
                      <p>
                        <strong>Address:</strong>
                        {cart.shippingDetails.address},{' '}
                        {cart.shippingDetails.city}{' '}
                        {cart.shippingDetails.postalCode},{' '}
                        {cart.shippingDetails.country}
                      </p>
                    </ListGroup.Item>

                    <ListGroup.Item>
                      <h2 className='my-2'>Payment Method</h2>
                      <strong>Method: </strong>
                      Razorpay
                    </ListGroup.Item>

                    <ListGroup.Item>
                      <h2 className='my-2'>Order Items</h2>
                      {cart.cartItems.length === 0 ? (
                        <Alert variant='info'>Cart is Empty!!</Alert>
                      ) : (
                        <ListGroup variant='flush'>
                          {cart.cartItems.map((item, index) => (
                            <ListGroup.Item key={index}>
                              <Row>
                                <Col md={1}>
                                  <Image
                                    src={`${item.image}`.replace('/server', '')}
                                    alt={item.name}
                                    fluid
                                    rounded
                                  />
                                </Col>
                                <Col>
                                  <Link to={`/product/${item._id}`}>
                                    {item.name}
                                  </Link>
                                </Col>
                                <Col md={4}>
                                  {item.quantity} x ${item.price} = $
                                  {(item.quantity * item.price).toFixed(2)}
                                </Col>
                              </Row>
                            </ListGroup.Item>
                          ))}
                        </ListGroup>
                      )}
                    </ListGroup.Item>
                  </ListGroup>
                </>
              ) : (
                <Row className='d-flex  justify-content-center'>
                  <Col xs={2}>
                    <Image src={orderPlaced} fluid rounded />
                  </Col>
                  <Col className='d-flex  align-items-center'>
                    <h1>Order Placed</h1>
                  </Col>
                </Row>
              )}
            </Col>

            <Col md={4}>
              <Card>
                <ListGroup variant='flush'>
                  <ListGroup.Item>
                    <h2>Order Summary</h2>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Items</Col>
                      <Col>${totalPrice}</Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Shipping</Col>
                      <Col>${shippingCharge}</Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Tax</Col>
                      <Col>${tax}</Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Total</Col>
                      <Col>${total}</Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    {/* {error && <Message variant='danger'>{error}</Message>} */}
                  </ListGroup.Item>
                  <ListGroup.Item>
                    {!paymentStatus ? (
                      <>
                        <Button
                          type='button'
                          className='btn-block'
                          disabled={cart.cartItems === 0}
                          onClick={placeOrderHandler}
                        >
                          Make Payment
                        </Button>
                      </>
                    ) : (
                      <Alert variant='success'>
                        Payment Completed Successfully
                      </Alert>
                    )}
                  </ListGroup.Item>
                </ListGroup>
              </Card>
            </Col>
          </Row>
        </>
      )}
    </>
  );
};

export default PlaceOrderScreen;
