import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import {
  Table,
  Form,
  Button,
  Row,
  Col,
  Container,
  Alert,
  Modal,
} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Loader from '../components/Loader';

const UserListScreen = () => {
  const [users, setUsers] = useState();
  const [error, setError] = useState();

  const user = useSelector((state) => state.data.user.loggedInUser);

  const getAllUsers = async () => {
    try {
      const allUsers = await axios.get('/api/user', {
        headers: {
          Authorization: `Bearer ${user.token}`,
        },
      });

      setUsers(allUsers.data.users);
    } catch (error) {
      setError(error.response.data.msg);
    }
  };

  useEffect(() => {
    getAllUsers();
  }, []);

  const removeUser = async (id) => {
    if (window.confirm('Please provide confirmation ?')) {
      const deletedUser = await axios.delete(`/api/user/${id}`, {
        headers: {
          Authorization: `Bearer ${user.token}`,
        },
      });
      getAllUsers();
    }
  };

  return (
    <>
      <h1>Users</h1>

      {!error ? (
        <>
          {users ? (
            <Table striped bordered hover responsive className='table-sm'>
              <thead>
                <tr>
                  <th>ID</th>
                  <th>NAME</th>
                  <th>EMAIL</th>
                  <th>ADMIN</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {users.map((user) => (
                  <tr key={user._id}>
                    <td>{user._id}</td>
                    <td>{user.name}</td>
                    <td>
                      <a href={`mailto:${user.email}`}>{user.email}</a>
                    </td>
                    <td>
                      {user.isAdmin ? (
                        <i
                          className='fas fa-check'
                          style={{ color: 'green' }}
                        ></i>
                      ) : (
                        <i
                          className='fas fa-times'
                          style={{ color: 'red' }}
                        ></i>
                      )}
                    </td>
                    <td>
                      <Button
                        variant='light'
                        className='btn-sm'
                        as={Link}
                        to={`/admin/user/edit/${user._id}`}
                      >
                        <i className='fas fa-edit'></i>
                      </Button>

                      <Button
                        variant='danger'
                        className='btn-sm'
                        onClick={() => removeUser(user._id)}
                      >
                        <i className='fas fa-trash'></i>
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </Table>
          ) : (
            <Loader />
          )}{' '}
        </>
      ) : (
        <Alert className='py-3' variant='danger'>
          {error}
        </Alert>
      )}
    </>
  );
};

export default UserListScreen;
