import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { Link, useParams } from 'react-router-dom';
import { Form, Button, Row, Col, Container, Modal } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import Loader from '../components/Loader';

const ProductEditScreen = () => {
  const { id } = useParams();

  const [name, setName] = useState('');
  const [price, setPrice] = useState(0);
  const [image, setImage] = useState('');
  const [brand, setBrand] = useState('');
  const [category, setCategory] = useState('');
  const [countInStock, setCountInStock] = useState(0);
  const [description, setDescription] = useState('');
  const [uploading, setUploading] = useState(false);
  const [loading, setLoading] = useState(false);
  const [updateLoading, setUpdateLoading] = useState(false);

  const [success, setSuccess] = useState(false);
  const user = useSelector((state) => state.data.user.loggedInUser);

  const getProductDetails = async () => {
    try {
      const { data } = await axios.get(`/api/products/${id}`);

      setLoading(true);

      setName(data.product.name);
      setPrice(data.product.price);
      setImage(data.product.image);
      setBrand(data.product.brand);
      setCountInStock(data.product.countInStock);
      setCategory(data.product.category);
      setDescription(data.product.description);
    } catch (error) {
      // console.log(error);
    }
  };

  useEffect(() => {
    getProductDetails();
  }, []);

  const submitHandler = async (e) => {
    setUpdateLoading(true);
    e.preventDefault();
    try {
      const res = await axios.put(
        `/api/products/${id}`,
        JSON.stringify({
          name,
          price,
          image,
          brand,
          category,
          countInStock,
          description,
        }),
        {
          headers: {
            Authorization: `Bearer ${user.token}`,
            'Content-Type': 'application/json',
          },
        }
      );

      if (res.status === 200) {
        setUpdateLoading(false);
        setSuccess(true);
        // setError('');
      }

      // console.log(res);
    } catch (err) {
      //   setError(err.response.data.message);
    }
  };

  const uploadFileHandler = async (e) => {
    const file = e.target.files[0];
    const formData = new FormData();
    formData.append('image', file);

    setUploading(true);

    try {
      const config = {
        headers: {
          'Content-type': 'multipart/form-data',
        },
      };

      const { data } = await axios.post('/api/upload', formData, config);

      setImage(data);

      setUploading(false);
    } catch (error) {
      // console.log(error);
      setUploading(false);
    }
  };

  return (
    <>
      {updateLoading ? <Loader /> : <></>}
      {success ? (
        <Modal
          size='sm'
          show={success}
          onHide={() => setSuccess(false)}
          aria-labelledby='example-modal-sizes-title-sm'
        >
          <Modal.Header closeButton>
            <Modal.Title id='example-modal-sizes-title-sm'>
              Successfull
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>Infomation Updated Successfully!!</Modal.Body>
        </Modal>
      ) : (
        <></>
      )}
      <Link to='/admin/productlist' className='btn btn-light my-3'>
        Go Back
      </Link>

      {loading ? (
        <Container>
          <Row className='justify-content-md-center'>
            <Col xs={12} md={6}>
              <Form onSubmit={submitHandler}>
                <Form.Group controlId='name'>
                  <Form.Label>Name</Form.Label>
                  <Form.Control
                    type='name'
                    placeholder='Enter name'
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                  ></Form.Control>
                </Form.Group>

                <Form.Group controlId='price'>
                  <Form.Label className='my-3'>Price</Form.Label>
                  <Form.Control
                    type='number'
                    placeholder='Enter price'
                    value={price}
                    onChange={(e) => setPrice(e.target.value)}
                  ></Form.Control>
                </Form.Group>

                <Form.Group controlId='image'>
                  <Form.Label className='my-3'>Image</Form.Label>
                  <Form.Control
                    type='text'
                    placeholder='Enter image url'
                    value={image}
                    onChange={(e) => setImage(e.target.value)}
                  ></Form.Control>

                  {uploading && <Loader />}
                </Form.Group>

                <Form.Group controlId='formFile' className='mt-3'>
                  <Form.Control
                    type='file'
                    id='image-file'
                    label='Choose File'
                    custom
                    onChange={uploadFileHandler}
                  ></Form.Control>
                </Form.Group>

                <Form.Group controlId='brand'>
                  <Form.Label className='my-3'>Brand</Form.Label>
                  <Form.Control
                    type='text'
                    placeholder='Enter brand'
                    value={brand}
                    onChange={(e) => setBrand(e.target.value)}
                  ></Form.Control>
                </Form.Group>

                <Form.Group controlId='countInStock'>
                  <Form.Label className='my-3'>Count In Stock</Form.Label>
                  <Form.Control
                    type='number'
                    placeholder='Enter countInStock'
                    value={countInStock}
                    onChange={(e) => setCountInStock(e.target.value)}
                  ></Form.Control>
                </Form.Group>

                <Form.Group controlId='category'>
                  <Form.Label className='my-3'>Category</Form.Label>
                  <Form.Control
                    type='text'
                    placeholder='Enter category'
                    value={category}
                    onChange={(e) => setCategory(e.target.value)}
                  ></Form.Control>
                </Form.Group>

                <Form.Group controlId='description'>
                  <Form.Label className='my-3'>Description</Form.Label>
                  <Form.Control
                    type='text'
                    placeholder='Enter description'
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                  ></Form.Control>
                </Form.Group>

                <Button type='submit' variant='primary' className='my-3'>
                  Update
                </Button>
              </Form>
            </Col>
          </Row>
        </Container>
      ) : (
        <Loader />
      )}
    </>
  );
};

export default ProductEditScreen;
