import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';

const initialState = {
  products: [],
  cart: {
    cartItems: [],
    shippingDetails: {},
    paymentMethod: '',
  },
};

export const fetchAllProducts = createAsyncThunk(
  'Products/fetchAllProducts',
  async () => {
    const res = await fetch('/api/products');

    const response = await res.json();

    // const response = await axios.get('http://127.0.0.1:3001/api/products');

    // console.log(response);

    return response;
  }
);

const productsSlice = createSlice({
  name: 'Products',
  initialState,
  reducers: {
    addItemToCart: (state, action) => {
      const { id, quantity, modification } = action.payload;
      let isAltered = false;

      if (state.cart.cartItems.length > 0) {
        for (let k of state.cart.cartItems) {
          if (k._id === id) {
            if (modification === 'add') {
              k.quantity = Number(k.quantity) + Number(quantity);

              for (let x of state.products) {
                if (x._id === id) {
                  x.countInStock = Number(x.countInStock) - Number(quantity);
                }
              }
            } else {
              k.quantity = Number(quantity);

              for (let x of state.products) {
                if (x._id === id) {
                  if (x.countInStock < quantity) {
                    x.countInStock = Number(x.countInStock) - Number(quantity);
                  } else {
                    x.countInStock = Number(x.countInStock) + Number(quantity);
                  }
                }
              }
            }

            isAltered = true;
          }
        }

        if (!isAltered) {
          const product = state.products.filter(
            (product) => product._id === id
          );
          product[0].quantity = quantity;
          state.cart.cartItems = [...state.cart.cartItems, product[0]];
        }
      } else {
        // console.log('y');
        const product = state.products.filter((product) => product._id === id);
        product[0].quantity = quantity;
        state.cart.cartItems = [...state.cart.cartItems, product[0]];
      }
    },
    removeItemFromCart: (state, action) => {
      // console.log(action.payload.id);
      state.cart.cartItems = state.cart.cartItems.filter(
        (item) => item._id !== action.payload.id
      );
    },
    emptyCart: (state) => {
      state.cart.cartItems = [];
    },
    saveShippingAddress: (state, action) => {
      state.cart.shippingDetails.address = action.payload.address;
      state.cart.shippingDetails.city = action.payload.city;
      state.cart.shippingDetails.postalCode = action.payload.postalCode;
      state.cart.shippingDetails.country = action.payload.country;
    },

    setPaymentType: (state, action) => {
      state.cart.paymentMethod = action.payload;
    },
  },

  extraReducers: (builder) => {
    builder.addCase(fetchAllProducts.pending, () => {
      //   console.log('Pending');
    });
    builder.addCase(fetchAllProducts.fulfilled, (state, action) => {
      state.products = action.payload.products;
      //   console.log('Fulfilled');
    });
    builder.addCase(fetchAllProducts.rejected, () => {
      //   console.log('Error');
    });
  },
});

export const {
  addItemToCart,
  removeItemFromCart,
  saveShippingAddress,
  setPaymentType,
  emptyCart,
} = productsSlice.actions;
export const productsReducer = productsSlice.reducer;
